<template>
    <div class="m-right-side" :class="{isHome: isHome}">
        <RightSideMsg>
            <em>官方反馈交流Q群</em> :
            <strong @click="onQQClick" class="u-link" title="点击复制">
                <a>{{ qq }}</a>
            </strong>
        </RightSideMsg>

        <div class="m-rank">
            <h2 class="m-title">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g fill="#ffa91e"><path d="M324.891 167.266l16.262 94.815L256 217.314l-85.153 44.767 16.262-94.815-68.891-67.16 95.205-13.833L256 0l42.577 86.273 95.205 13.833z"/><path d="M409.944 253.46v87.673L256.9 403.303l-.9-.36-153.944-61.76V253.46L256 315.219z"/><path d="M409.944 362.157v87.673L256.9 512l-.9-.36-153.944-61.76v-87.723L256 423.916z"/></g><g fill="#ff8900"><path d="M341.153 262.081L256 217.314V0l42.577 86.273 95.205 13.833-68.891 67.16zM409.944 253.46v87.673L256.9 403.303l-.9-.36v-87.724zM256 423.916l153.944-61.759v87.673L256.9 512l-.9-.36z"/></g></svg>
                <span class="u-text">贡献榜</span>
            </h2>
            <el-tabs v-model="active_rank_type">
                <el-tab-pane
                    v-for="(type, key) in rank_types"
                    :key="key"
                    :label="type.name"
                    :name="type.sub"
                >
                    <ul class="u-list">
                        <li v-for="(rank, k) in ranks" :key="k">
                            <a
                                class="u-contributor"
                                :href="rank.user_id ? author_url(rank.user_id) : null"
                            >
                                <i class="u-avatar">
                                    <img
                                        :src="rank.user_avatar | resolveAvatarPath"
                                        :alt="rank.user_nickname"
                                    />
                                </i>
                                <span class="u-name" v-text="rank.user_nickname"></span>
                                <em class="u-count">+ {{ rank.total_score }}</em>
                            </a>
                        </li>
                    </ul>
                </el-tab-pane>
            </el-tabs>
        </div>

        <div class="m-group">
            <h2 class="m-title">
                <svg height="512" viewBox="0 0 512 512" width="512" xmlns="http://www.w3.org/2000/svg" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M256 256h85.333v21.333C341.333 300.897 360.436 320 384 320s42.667-19.103 42.667-42.667V256H512V42.667C512 19.103 492.897 0 469.333 0H256z" fill="#dd636e"/><path d="M469.333 0H384v320c23.564 0 42.667-19.103 42.667-42.667V256H512V42.667C512 19.103 492.897 0 469.333 0z" fill="#d82e3d"/><path d="M256 256v-85.333h21.333C300.897 170.667 320 151.564 320 128s-19.103-42.667-42.667-42.667H256V0H42.667C19.103 0 0 19.103 0 42.667V256z" fill="#ffe646"/><path d="M277.333 85.333H256V0H128v256h128v-85.333h21.333C300.898 170.667 320 151.564 320 128s-19.102-42.667-42.667-42.667z" fill="#ffc81e"/><path d="M256 256h-85.333v-21.333C170.667 211.102 151.564 192 128 192s-42.667 19.102-42.667 42.667V256H0v213.333C0 492.897 19.103 512 42.667 512H256z" fill="#d49eee"/><path d="M256 512V256h-85.333v-21.333C170.667 211.103 151.564 192 128 192v320z" fill="#b77ede"/><path d="M256 256v85.333h-21.333C211.102 341.333 192 360.436 192 384s19.102 42.667 42.667 42.667H256V512h213.333C492.897 512 512 492.897 512 469.333V256z" fill="#badc1e"/><path d="M384 512h85.333C492.897 512 512 492.897 512 469.333V256H384z" fill="#73be14"/><path d="M341.333 249v28.333C341.333 300.897 360.436 320 384 320s42.667-19.103 42.667-42.667V249z" fill="#dd636e"/><path d="M384 246v74c23.564 0 42.667-19.103 42.667-42.667V246z" fill="#d82e3d"/></svg>
                <span class="u-text">各区服成就群</span>
                <a
                    class="u-show-message"
                    target="_blank"
                    href="https://www.jx3box.com/tool/13659/"
                >申请展示</a>
            </h2>
            <el-collapse accordion>
                <el-collapse-item
                    v-for="(server, key) in groups"
                    :title="key"
                    :key="key"
                    :name="key"
                >
                    <ul class="u-groups">
                        <li v-for="(group, k) in server" :key="k">
                            <el-tag
                                class="u-platform"
                                v-if="group.platform == 'QQ'"
                                size="mini"
                                v-text="`${group.platform}群`"
                            ></el-tag>
                            <el-tag
                                class="u-platform"
                                v-if="group.platform == 'YY'"
                                size="mini"
                                type="info"
                                v-text="`${group.platform}群`"
                            ></el-tag>
                            <el-button
                                class="u-number"
                                size="mini"
                                v-text="group.number"
                                v-clipboard:copy="group.number"
                                v-clipboard:success="copy_success"
                                v-clipboard:error="copy_error"
                            ></el-button>
                        </li>
                    </ul>
                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
import { authorLink } from "@jx3box/jx3box-common/js/utils";
import { get_groups } from "../service/group";
import { getAchievementRanking } from "../service/achievement";
import { __ossRoot, __ossMirror } from '@jx3box/jx3box-common/data/jx3box.json'

export default {
    name: "Extend",
    data() {
        return {
            active_rank_type: "11",
            rank_types: [
                { sub: "11", name: "秘境" },
                { sub: "7", name: "任务" },
                { sub: "10", name: "声望" },
                { sub: "40", name: "家园" },
                { sub: "5", name: "技艺" },
                { sub: "9", name: "战斗" },
                { sub: "6", name: "阅读" },
                { sub: "16", name: "活动" },
                { sub: "8", name: "足迹" },
                { sub: "15", name: "节日" },
            ],
            ranks: null,
            groups: null,
            isHome: true,

            qq: "614370825",
        };
    },
    methods: {
        author_url: authorLink,
        copy_success() {
            this.$notify({ title: "复制成功", type: "success" });
        },
        copy_error() {
            this.$notify({ title: "浏览器不支持", type: "error" });
        },
        get_users_ranks(sub) {
            let that = this;
            getAchievementRanking(sub).then(
                (data) => {
                    data = data.data;
                    that.ranks = data.data.ranking;
                },
                () => {
                    that.ranks = false;
                }
            );
        },
        checkIsHome: function () {
            this.isHome = this.$route.name == "home" || !this.$route.name;
        },
        onQQClick() {
            navigator.clipboard.writeText(this.qq).then(() => {
                this.$notify({
                    title: "复制成功",
                    message: "内容：" + this.qq,
                    type: "success",
                });
            })
        }
    },
    mounted() {
        // 获取成就群
        get_groups("achievement", {
            order_by: "server",
            group_by: "server",
        }).then(
            (data) => {
                data = data.data;
                if (data.code === 200) {
                    this.groups = data.data.groups;
                }
            },
            () => {
                this.groups = false;
            }
        );
        this.checkIsHome();
    },
    watch: {
        active_rank_type: {
            immediate: true,
            handler() {
                this.get_users_ranks(this.active_rank_type);
            },
        },
        "$route.name": function () {
            this.checkIsHome();
        },
    },
    filters: {
        resolveAvatarPath: function (val) {
            return val.replace(__ossRoot, __ossMirror);
        },
    },
};
</script>

<style lang="less">
@import "../assets/css/components/right_side.less";
</style>
